.TextWithImageContainer {
  background-color: #E8E8E8;
  padding: 30px 50px 15px 50px;
}
@media (min-width: 1440px) {
  .TextWithImageContainer .TextWithImageContentContainer {
    width: 1440px;
    margin: auto;
  }
}
@media (min-width: 1920px) {
  .TextWithImageContainer .TextWithImageContentContainer {
    width: 1600px;
    margin: auto;
  }
}
.TextWithImageContainer .TextWithImageContentContainer .TextColumn {
  margin: 0 auto;
}
@media (min-width: 992px) {
  .TextWithImageContainer .TextWithImageContentContainer .TextColumn {
    width: 85%;
  }
}
.TextWithImageContainer .TextWithImageContentContainer .TextColumn p {
  line-height: 30px;
}
@media (min-width: 992px) {
  .TextWithImageContainer .TextWithImageContentContainer .TextColumn p {
    text-align: justify;
    font-size: 18px;
  }
}



